<template>
  <!-- 第一种 -->

  <section
    v-if="currentSong.song || currentSong.ar"
    class="playbar"
    :class="{ playing: playing }"
    @click="$emit('toggle-show-play-page', true)"
  >
    <li>
      <img
        :src="`${
          currentSong.song ? currentSong.picUrl : currentSong.al.picUrl
        }?imageView=1&type=webp&thumbnail=60x0`"
        alt=""
      />
    </li>
    <li class="lis">
      {{ currentSong.name }}
      <span
        >-
        {{
          currentSong.song
            ? currentSong.song.artists[0].name
            : currentSong.ar[0].name
        }}</span
      >
    </li>
    <li class="liscan">
      <!-- <span @click="$emit('toggle-playing-state')">播放</span> -->
      <div class="progress" @click.stop="$emit('toggle-playing-state')">
        <canvas width="40" height="40" ref="canvas"></canvas>
        <div class="icon" :class="[playing ? 'pause' : 'play']"></div>
      </div>
      <span @click.stop="$emit('toggle-show-play-list', true)">
        <i class="bi bi-text-indent-right mybook"></i>
      </span>
    </li>
  </section>

  <section
    v-else
    class="playbar"
    :class="{ playing: playing }"
    @click="$emit('toggle-show-play-page', true)"
  >
    <li>
      <!-- //////////////////搜索歌词    上面  v-if="currentSong.song || currentSong.ar"  v-else -->
      <img :src="picUrl" alt="" />
    </li>
    <li class="lis">
      {{ currentSong.name }}
      <span>- {{ currentSong.artists[0].name }}</span>
    </li>
    <li class="liscan">
      <!-- <span @click="$emit('toggle-playing-state')">播放</span> -->
      <div class="progress" @click.stop="$emit('toggle-playing-state')">
        <canvas width="40" height="40" ref="canvas"></canvas>
        <div class="icon" :class="[playing ? 'pause' : 'play']"></div>
      </div>
      <span @click.stop="$emit('toggle-show-play-list', true)">
        <i class="bi bi-text-indent-right mybook"></i>
      </span>
    </li>
  </section>

  <!-- <section class="play-list" v-if="showPlayList">
      <div
        class="mask animate__animated animate__fadeIn"
        @click="showPlayList = false"
      ></div>
      <div class="swiper animate__animated animate__backInUp">
        <div class="card">
          <ul class="list">
            <SongList
              v-for="(item, index) in currentPlayList"
              :key="item.id"
