  <!-- 第二种 -->
  <!-- <div v-else class="playbar" :class="{ playing: playing }">
    <li>
      <img
        :src="`${currentSong.al.picUrl}?imageView=1&type=webp&thumbnail=60x0`"
        alt=""
      />
    </li>
    <li class="lis">
      {{ currentSong.name }} <span>- {{ currentSong.ar[0].name }}</span>
    </li>
    <li>
      <div class="progress"  @click="$emit('toggle-playing-state')">
        <canvas width="40" height="40" ref="canvas"></canvas>
        <div class="icon" :class="[playing ? 'pause' : 'play']"></div>
      </div>
      <span>列表</span>
    </li>
  </div> -->
</template>

<script>
// import SongList from "@/components/SongList.vue";

export default {
  // components: {
  //   //第三：注册
  //   SongList,
  // },
  props: {
    currentSong: Object,
    playing: Boolean, // 接收父传的:playing="playing"  实现图片转圈
    currentTime: Number, //接收父传的:currentTime="currentTime"  实现歌曲时长
    duration: Number, //接收父传的:duration="duration"  实现歌曲全部时长
    currentPlayList: Array, //接收父传来的 :currentPlayList="currentPlayList"  实现卡片
    picUrl: String,//////////////////搜索歌词    上面  v-if="currentSong.song || currentSong.ar"  v-else
  },
  // data: function () {
  //   //自己的状态
  //   return {
  //     showPlayList: false, // showPlayPage: false,
  //     showPlayPage: false,
  //   };
  // },
  mounted: function () {
    // console.log(this.$refs.canvas);
    // this.canvas = this.$refs.canvas
  },
  computed: {
    percentage: function () {
      //b百分比
      return this.currentTime / this.duration;
    },
  },
  watch: {
    //监听playing变化  然后开始画canvas
    percentage: function (n) {
      //n是百分比  需要乘于
      // console.log(n)
      var context = this.$refs.canvas.getContext("2d");
      context.clearRect(0, 0, 40, 40); //清除画框
      context.beginPath();
      context.arc(
        18,
        18,
        16,
        (Math.PI / 180) * (0 - 90),
        (Math.PI / 180) * (360 * n - 90)
      ); // arc是圆弧  x18（宽一半）  y18（高一半）  半径16（图片一半）
      context.strokeStyle = "#d43c33";
      context.lineWidth = 2; //描边的宽度
      context.stroke(); //1.先描边
      // context.closePath();//闭合

      context.beginPath();
      context.arc(
        18,
        18,
        16,
        (Math.PI / 180) * (360 * n - 90),
        (Math.PI / 180) * (360 - 90)
      );
      context.strokeStyle = "gray";
      context.lineWidth = 1;
      context.stroke();
    },
  },
};
</script>

<style lang="less" scoped>
.playbar {
  width: 100%;
  height: 50px;
  padding-bottom: 10px;
  background-color: rgba(255, 255, 255, 0.87);
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;

  display: flex;
  /* flex-wrap: nowrap; */
  align-content: center;
  justify-content: space-between;
  align-items: center;

  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.3); //音频边框阴影
  li {
    list-style: none;
    display: flex;
    justify-content: space-between;
    align-content: center;
    align-items: center;
    margin-right: 10px;
    img {
      width: 35px;
      height: 35px;
      margin-left: 12px;
      text-align: center;
      // line-height: 35px;
      border-radius: 50%;

      animation: rotate 8s linear infinite;
      animation-play-state: paused;
    }
  }
  .lis {
    // padding: 0px 10px;
    flex: 1;
    font-size: 14px;

    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
    overflow: hidden;
    text-overflow: ellipsis;
    span {
      color: rgb(128, 121, 120);
      font-size: 13px;
    }
  }
  .progress {
    width: 30px;
    height: 30px;
    margin-top: 5px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    // canvas当图片进行压缩
    // display: flex;
    canvas {
      width: 100%;
      height: 100%;
      margin-left: 1px;
      margin-top: 1px;
      position: absolute;
      top: 0;
      left: 0;
    }
    .icon {
      width: 12px;
      height: 12px;
      display: flex;
      justify-content: space-around;
      align-items: center;
      &.play {
        // background: lightcoral;
        border-top: 6px solid transparent;
        // border-right: 0px solid rgb(0, 132, 255);
        border-bottom: 6px solid transparent;
        border-left: 10px solid rgb(0, 0, 0);
        transform: translateX(1px);
        // transform: translateY(1px);
        margin-left: 1px;
      }
      &.pause {
        // width: 15px;
        // height: 15px;
        // display: flex;
        // justify-content: space-around;
        &::before,
        &::after {
          content: "";
          display: block;
          height: 80%;
          width: 20%;
          background: black;
        }
      }
    }
  }
  .liscan {
    display: flex;
    /* flex-wrap: nowrap; */
    justify-content: center;
    align-items: center;
    span {
      display: flex;
      align-items: center;
      justify-content: center;
      align-content: center;
      i.mybook {
        font-size: 25px;
        margin-top: 7px;
        margin-left: 3px;
      }
    }
  }

  &.playing {
    li img {
      animation-play-state: running;
    } //小心层级问题
  }
}

