<template>
  <!-- songlist使用这个 -->
  <li
    v-if="item.song"
    class="songlist"
    @click="$emit('change-current-song', item)"
  >
    <div class="left">
      <div class="left-foo">
        {{ item.name }}
        <span v-for="n in item.song.alias" :key="n">{{ n }}</span>
      </div>
      <div class="left-bot">
        <em v-if="item.song.exclusive"></em>
        <i
          class="artist"
          v-for="artist in item.song.artists"
          :key="artist.id"
          >{{ artist.name }}</i
        >
        <b>{{ item.song.album.name }}</b>
      </div>
    </div>
    <div class="right">
      <div
        class="play"
        :class="{ current: currentSongId === item.id, playing: playing }"
      >
        <i></i>
        <i></i>
        <i></i>
      </div>
    </div>
  </li>

<!-- playlist使用这个 -->
  <li
    v-else-if="item.ar"
    class="songlist"
    @click="$emit('change-current-song', item)"
  >
    <div class="num"><slot></slot></div>
    <!-- 插槽 -->
    <div class="left">
      <div class="left-foo">
        {{ item.name }}
        <span v-for="n in item.alia" :key="n">{{ n }}</span>
      </div>
      <div class="left-bot">
        <!-- <em v-if="item.song.exclusive"></em> -->
        <i class="artist" v-for="artist in item.ar" :key="artist.id">{{
          artist.name
        }}</i>
        <b>{{ item.al.name }}</b>
      </div>
    </div>
    <div class="right">
      <div
        class="play"
        :class="{ current: currentSongId === item.id, playing: playing }"
      >
        <i></i>
        <i></i>
        <i></i>
      </div>
    </div>
  </li>

  <!-- searchsong使用 -->
  <li v-else class="songlist" @click="$emit('change-current-song', item)">
    <div class="num"><slot></slot></div>
    <div class="left">
      <div class="left-foo">
        {{ item.name }}
        <span v-for="n in item.alias" :key="n">{{ n }}</span>
      </div>
      <div class="left-bot">
        <em ></em>
        <i class="artist" v-for="artist in item.artists" :key="artist.id">{{
          artist.name
        }}</i>
        <b>{{ item.name }}</b>
      </div>
    </div>
    <div class="right">
      <div
        class="play"
        :class="{ current: currentSongId === item.id, playing: playing }"
      >
        <i></i>
        <i></i>
        <i></i>
      </div>
    </div>
  </li>
</template>

<script>
export default {
  props: {
    item: {
      type: Object,
      required: true,
    },
    currentSongId: {
      type: Number,
    },
    playing: Boolean, //接收父传下来的音频事件 实现动画
    ChangeCurrentSong: Object,
  },
};
</script>

<style lang="less" scoped>
//scoped会自动有一个属性过滤选择器
.songlist {
  padding: 5px 10px 5px 0;
  display: flex;
  align-items: center;
  flex-wrap: nowrap;

  border-bottom: 1px solid rgba(212, 212, 212, 0.4); //会顶住两边  改padding加margin
  margin-left: 13px;
  .num {
    padding: 0 12px 0 0px;
  }
  &.lt3 {
    .num {
      color: #d43c33;
    }
  }
  .left {
    flex: 1;
    .left-foo {
      margin-right: 15px;

      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 1;
      overflow: hidden;
      text-overflow: ellipsis;
      span {
        color: #888;
        &::before {
          content: "(";
        }
        &::after {
          content: ")";
        }
      }
    }
    .left-bot {
      font-size: 10px;
      color: #888;
      line-height: 1.5;

      // 显示两行
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 1;
      overflow: hidden;
      text-overflow: ellipsis;
      em {
        display: inline-block;
        width: 12px;
        height: 8px;
        // background: red;
        background-image: url("https://s3.music.126.net/mobile-new/img/index_icon_2x.png");
        background-repeat: no-repeat;
        background-size: 166px auto;
        margin-right: 4px;
      }
      i.artist {
        font-style: normal;
        &::after {
          content: "/";
          margin: 0 3px;
        }
        &:last-of-type {
          //最后一个不加斜杠  因为标签才可以实现：last-of-type 所以上面要改为标签的 i b
          //   background: red;
          &::after {
            content: "-";
            margin: 0 5px;
          }
        }
      }
      b {
        font-weight: normal;
      }
    }
  }
  .right {
    width: 22px;
    height: 22px;
    //   background: red;
    //   display: flex;

    margin-left: 10px;
    position: relative;

    display: flex;
    justify-content: center;
    align-items: center;
    .play {
      // position: absolute;
      // top: 0;
      // left: 0;
      width: 100%;
      height: 100%;

      background-image: url("https://s3.music.126.net/mobile-new/img/index_icon_2x.png");
      background-repeat: no-repeat;
      background-size: 166px auto;
      background-position: -24px 0;
    }
    .current {
      // background: red;
      width: 18px;
      height: 18px;
      background: none;
      display: flex;
      justify-content: space-around;
      i {
        width: calc(100% / 7.5);
        // width: 3px;
        height: 100%;
        // display: inline-block;
        background: #d43c33;
        transform-origin: bottom; //改变变化坐标 从小面开始变化
        animation: playing 1s linear 0s infinite alternate;
        animation-play-state: paused; //只有在paused才加playing
        &:nth-child(1) {
          animation-delay: -0.6s; //控制每一个不同
        }
        &:nth-child(2) {
          animation-delay: -0.3s;
        }
      }
      &.playing {
        //写在内部
        i {
          animation-play-state: running;
        }
      }
    }
  }
}

@keyframes playing {
  //加动画效果
  from {
    transform: scaleY(0.2); //缩放
  }
  to {
    transform: scaleY(1); //缩放
  }
}
</style>