<template>
  <section class="play-page">
    <!-- 背景遮罩 -->
    <div
      v-if="currentSong.song || currentSong.ar"
      class="mask"
      :style="{
        backgroundImage: `url(${
          currentSong.song ? currentSong.picUrl : currentSong.al.picUrl
        }?imageView=1&type=webp&thumbnail=246x0)`,
      }"
    ></div>
    <!-- //////////////////搜索歌词    上面  v-if="currentSong.song || currentSong.ar"  v-else -->
    <div
      v-else
      class="mask"
      :style="{
        backgroundImage: 'url(' + picUrl + ')',
      }"
    ></div>
    <!-- 下拉 -->
    <div class="xiala" v-if="currentSong.song || currentSong.ar">
      <button @click="$emit('toggle-show-play-page', false)" class="but">
        <i class="bi bi-chevron-double-down mybook"></i>
      </button>
      <div class="spans">
        <p class="spana">{{ currentSong.name }}</p>
        <p class="spanb">
          {{
            currentSong.song
              ? currentSong.song.artists[0].name
              : currentSong.ar[0].name
          }}
        </p>
      </div>
    </div>
    <div class="xiala" v-else>
      <button @click="$emit('toggle-show-play-page', false)" class="but">
        <i class="bi bi-chevron-double-down mybook"></i>
      </button>
      <div class="spans">
        <p class="spana">{{ currentSong.name }}</p>
        <p class="spanb">
          {{ currentSong.artists[0].name }}
        </p>
      </div>
    </div>
    <!-- 下拉 -->

    <Lyric
      v-show="showplayback"
      :currentSongId="currentSong.id"
      :currentSong="currentSong"
      :currentTime="currentTime"
      :playing="playing"
      @toggle-show-play-back="showplayback = $event"
    />
    <!-- 旋转图 -->
    <PlayIng
      v-show="!showplayback"
      :picUrl="picUrl"
      :currentSong="currentSong"
      :playing="playing"
      @toggle-show-play-back="showplayback = $event"
      @change-current-song="$emit('change-current-song', $event)"
    />

    <!-- <section class="rotate"> 
      <img
        class="needle"
        :class="{ paused: !playing }"
        src="https://s3.music.126.net/mobile-new/img/needle-ab.png"
        alt=""
      />
      <section class="record" :class="{ playing: playing }">
        <img
          class="thumb"
          :src="currentSong.song ? currentSong.picUrl : currentSong.al.picUrl"
          alt=""
        />
        <img
          class="disc"
          src="https://s3.music.126.net/mobile-new/img/disc.png"
          alt=""
        />
      </section>
    </section> -->

    <!-- 进度条开始 -->
    <section class="progress">
      <span class="currentTimetime">{{ currentTime | playTime }}</span>
      <input
        type="range"
        :max="duration"
        step="0.5"
        v-model="value"
        @change="progressChange"
        @input="progressInput"
      />
      <span
        class="bar"
        :style="{ width: (value / duration) * 100 + '%' }"
      ></span>
      <span class="durationtime">{{ duration | playTime }}</span>
      <!-- <input type="range" :max="duration" step="0.5" v-model="value" /> -->
    </section>
    <!-- 进度条结束 -->

    <!-- 按钮 -->
    <section class="controls">
      <span @click="$emit('toggle-play-model')">
        <i class="bi bi-arrow-left-right mybooks"></i>
      </span>
      <span @click="$emit('prev-song')">
        <i class="bi bi-caret-left mybooks"></i>
      </span>
      <span @click="$emit('toggle-playing-state')">
        <i class="bi bi-pause-circle mybooks" v-if="playing"></i>
        <i class="bi bi-play-circle mybooks" v-else></i>
      </span>
      <span @click="$emit('next-song')">
        <i class="bi bi-caret-right mybooks"></i>
      </span>
      <span @click.stop="$emit('toggle-show-play-list', true)">
        <i class="bi bi-text-indent-right mybooks"></i>
      </span>
    </section>
  </section>
</template>

<script>
import Lyric from "@/components/Lyric.vue";
import PlayIng from "@/components/PlayIng.vue";
export default {
  components: {
    Lyric,
    PlayIng,
  },
  props: {
    currentSong: Object,
    playing: Boolean, // 接收父传的:playing="playing"  实现图片转圈
    currentTime: Number, //接收父传的:currentTime="currentTime"  实现歌曲时长
    duration: Number, //接收父传的:duration="duration"  实现歌曲全部时长
    picUrl: String, //////////////////搜索歌词    上面  v-if="currentSong.song || currentSong.ar"  v-else
  },
  data: function () {
    return {
      showplayback: true, //歌词
      value: this.currentTime,
      inputing: false,

      //  formateTime: null, //当前值（时间过滤器）
      // time: Date.now(), //初始值（时间过滤器）
      s: this.duration, //全部（歌曲时间过滤器）
      m: this.currentTime, //当前（歌曲时间过滤器）
      playTime: null,//过滤器
    };
  },
  watch: {
    currentTime: function (n) {
      if (!this.inputing) {
        this.value = n;
      }
    },
  },
  methods: {
    progressChange: function () {
      this.inputing = false;
      this.$emit("current-time-change", event.target.value);
    },
    progressInput: function () {
      this.inputing = true;
    },
  },
  filters: {//歌曲时间显示（过滤器）
    playTime: function (data) {
      let m = parseInt(data / 60);
      let s = parseInt(data % 60);
      if (m == 0 && s == 0) {
        return "0:00";
      } else {
        return m + ":" + (s < 10 ? "0" + s : s);
      }
    },
  },
};
</script>

