  //     transform-origin: 15px 15px;
  //     transform: rotate(0deg);
  //     transition: all 0.3s;
  //     &.paused {
  //       transform: rotate(-20deg); //暂停时的指针
  //     }
  //   }
  //   .record {
  //     position: relative;
  //     width: 80vw;
