  // 进度条
  .progress {
    width: 80vw;
    height: 4px;
    margin: 0px auto;
    // margin-top: 50px;
    background: white;
    position: fixed;
    left: 36px;
    bottom: 130px;
    border-radius: 10px;
    .currentTimetime {
      color: red;
      position: absolute;
