<template>
  <div class="play" :class="{ playing: playing }">
    <transition
      name="custom-classes-transition"
      enter-active-class="animate__animated animate__fadeIn"
      leave-active-class="animate__animated animate__fadeOut"
    >
      <keep-alive>
        <PlayPage
          v-if="showPlayPage"
          @toggle-show-play-page="showPlayPage = $event"
          @toggle-show-play-list="showPlayList = $event"
          :currentSong="currentSong"
          :currentTime="currentTime"
          :duration="duration"
          :playing="playing"
          :picUrl="picUrl"
          @toggle-playing-state="$emit('toggle-playing-state')"
          @next-song="$emit('next-song')"
          @prev-song="$emit('prev-song')"
          @toggle-play-model="$emit('toggle-play-model')"
          @current-time-change="$emit('current-time-change', $event)"
        />
      </keep-alive>
    </transition>
    <transition
      name="custom-classes-transition"
      enter-active-class="animate__animated animate__slideInUp"
      leave-active-class="animate__animated animate__slideOutDown"
    >
      <PlayBar
        v-show="!showPlayPage"
        :currentSong="currentSong"
        :currentTime="currentTime"
        :duration="duration"
        :currentPlayList="currentPlayList"
        :playing="playing"
        :picUrl="picUrl"
        @toggle-playing-state="$emit('toggle-playing-state')"
        @toggle-show-play-list="showPlayList = $event"
        @toggle-show-play-page="showPlayPage = $event"
      />
    </transition>

    <PlayList
      v-if="showPlayList"
      :currentPlayList="currentPlayList"
      :currentSong="currentSong"
      :playing="playing"
      @toggle-show-play-list="showPlayList = $event"
      @change-current-song="$emit('change-current-song', $event)"
    />
  </div>
</template>

<script>
import PlayBar from "@/components/PlayBar.vue";
import PlayPage from "@/components/PlayPage.vue";
import PlayList from "@/components/PlayList.vue";
export default {
  components: {
    PlayBar,
    PlayPage,
    PlayList,
  },
  props: {
    ChangeCurrentSong: Object,
    currentSong: Object,
    playing: Boolean, // 接收父传的:playing="playing"  实现图片转圈
    currentTime: Number, //接收父传的:currentTime="currentTime"  实现歌曲时长
    duration: Number, //接收父传的:duration="duration"  实现歌曲全部时长
    currentPlayList: Array, //接收父传来的 :currentPlayList="currentPlayList"  实现卡片
  },
  data: function () {
    //自己的状态
    return {
      showPlayList: false, // showPlayPage: false,
      showPlayPage: false,
      picUrl:null,//////////////////搜索歌词    上面 :playing="playing"
    };
  },
  methods: {//////////////////搜索歌词
    grt: function (id) {
      this.axios
        .get("https://apis.netstart.cn/music/song/detail", {
          params: {
            ids: id,
          },
        })
        .then((res) => {
          // console.log(res); //拿到数据
          this.picUrl = res.data.songs[0].al.picUrl; //得到数据上面就遍历显示
          console.log(this.picUrl)//拿到数据
        });

    },
  },
  created: function () {//////////////////搜索歌词
    this.grt(this.currentSong.id);
  },
};
</script>

<style lang="less" scoped>
</style>