              <path
                d="M8 11a3 3 0 1 1 0-6 3 3 0 0 1 0 6zm0 1a4 4 0 1 0 0-8 4 4 0 0 0 0 8z"
              />
              <path d="M9.5 8a1.5 1.5 0 1 1-3 0 1.5 1.5 0 0 1 3 0z" />
            </svg>
          </span>
          悦心音乐
        </h1>
        <div class="foo_right">下载APP</div>
      </div>
      <ul id="nav">
        <li><router-link to="/">推荐歌单</router-link></li>
        <li><router-link to="/hot">热歌榜</router-link></li>
        <li>
          <router-link to="/search">搜索</router-link>
        </li>
      </ul>
    </div>
    <section class="routes">
      <transition
        name="custom-classes-transition"
        enter-active-class="animate__animated animate__slideInRight"
        leave-active-class="animate__animated animate__slideOutLeft"
      >
        <!-- mode="out-in" -->
        <!-- <keep-alive> -->
        <router-view
          @change-current-song="ChangeCurrentSong"
          @change-current-play-list="changeCurrentPlayList"
          :currentSongId="currentSong ? currentSong.id : null"
          :playing="playing"
          style="
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            overflow-y: auto;
          "
        />
        <!-- </keep-alive> -->
      </transition>
    </section>
    <audio
      ref="audio"
      :src="currentSongUrl"
      style="height: 40px; margin-bottom: 40px"
      autoplay
      @playing="playing = true"
      @pause="playing = false"
      @timeupdate="timeupdate"
      @durationchange="durationchange"
    ></audio>
    <Play
      v-if="currentSong"
      :currentSong="currentSong"
      @toggle-playing-state="togglePlayingState"
      :playing="playing"
      :currentTime="currentTime"
      :duration="duration"
      :currentPlayList="currentPlayList"
      @change-current-song="ChangeCurrentSong"
      @next-song="nextSong"
      @prev-song="prevSong"
      @current-time-change="$refs.audio.currentTime = $event"
    ></Play>
  </div>
</template>

<script>
import Play from "@/components/Play.vue";

export default {
  components: {
    Play,
  },
  data: function () {
    return {
      currentSong: null,
      currentPlayList: [],
      playing: false, //一开始状态为不动

      currentTime: 0,
      duration: 0,
    };
  },
  computed: {
    currentSongUrl: function () {
      //播放地址  上面播放条直接调用currentSongUrl
      if (this.currentSong) {
        //判断存不存在，存在就为id
        return `https://music.163.com/song/media/outer/url?id=${this.currentSong.id}.mp3`; //传id
      } else {
        //否则null
        return null;
      }
    },
  },
  methods: {
    changeCurrentSong: function (song) {
      //调用方法，在下面上下一首使用
      // console.log(song);
      this.currentSong = song;
    },
    ChangeCurrentSong: function (song) {
      //注意跟上面有大小写区别
      //可以带参数song
      // console.log(song);//检查songlist传到home再传到app的数据
      this.currentSong = song; //子传来的数据
    },
    changeCurrentPlayList: function (list) {
      this.currentPlayList = list; //得到 @change-current-play-list="changeCurrentPlayList"  还往playbar传
    },
    togglePlayingState: function () {
      if (this.playing) {
        //判断playing状态   ref="audio"
        // 暂停歌曲
        this.$refs.audio.pause();
      } else {
        // 播放歌曲
        this.$refs.audio.play();
      }
    },
    timeupdate: function (event) {
      //歌曲播放时长（得往下传给播放按钮）
      // console.log(event.target.currentTime)
      this.currentTime = event.target.currentTime;
    },
    durationchange: function (event) {
      //全部时长（得往下传给播放按钮）
      // console.log(event.target.duration)
      this.duration = event.target.duration;
    },
    nextSong: function () {
      //下一首
      // console.log(123);
      var index = this.currentPlayList.findIndex((item) => {
        //findIndex查找索引
        return item.id === this.currentSong.id;
      });
      index++; //先加再取
      index = index > this.currentPlayList.length - 1 ? 0 : index; //注意：如果考虑等于=，是否能从最后一个取到第一个
      index = index < 0 ? this.currentPlayList.length - 1 : index;
      // index = index <= 0 ? this.currentPlayList.length - 1 : index;//注意等于重突
      // console.log(index);
      this.changeCurrentSong(this.currentPlayList[index]); //调用方法
    },
    prevSong: function () {
      //上一首
      // console.log(456);
      var index = this.currentPlayList.findIndex((item) => {
        return item.id === this.currentSong.id;
      });
      index--; //先减再取
      // index = index >= this.currentPlayList.length - 1 ? 0 : index;//注意等于重突
      index = index > this.currentPlayList.length - 1 ? 0 : index;
      index = index < 0 ? this.currentPlayList.length - 1 : index;
      // console.log(index);
      this.changeCurrentSong(this.currentPlayList[index]);
    },
  },
};
</script>

