<template>
  <section class="rotate" @click="$emit('toggle-show-play-back',true)">
    <img
      class="needle"
      :class="{ paused: !playing }"
      src="https://s3.music.126.net/mobile-new/img/needle-ab.png"
      alt=""
    />
    <section class="record" :class="{ playing: playing }">
      <!-- //////////////////搜索歌词     :src="picUrl"-->
      <img
        class="thumb"
        :src="picUrl"
        alt=""
      />
      <img
        class="disc"
        src="https://s3.music.126.net/mobile-new/img/disc.png"
        alt=""
      />
    </section>
  </section>
</template>

<script>
export default {
  props: {
    currentSong: Object,
    playing: Boolean, // 接收父传的:playing="playing"  实现图片转圈
    currentTime: Number, //接收父传的:currentTime="currentTime"  实现歌曲时长
    duration: Number, //接收父传的:duration="duration"  实现歌曲全部时长
    picUrl:String,//////////////////搜索歌词    上面 :playing="playing"
    // currentPlayList: Array, //接收父传来的 :currentPlayList="currentPlayList"  实现卡片
  },
};
</script>

<style lang="less" scoped>
.pos-ab() {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.rotate {
  position: relative;
  padding-top: 25vw;
  img.needle {
    height: 40vw;
    height: 40vw;
    position: absolute;
    top: 0;
    left: 50%;
    z-index: 9;
    margin-left: -15px;
    transform-origin: 15px 15px;
    transform: rotate(0deg);
    transition: all 0.3s;
    &.paused {
      transform: rotate(-20deg); //暂停时的指针
    }
  }
  .record {
    position: relative;
    width: 80vw;
    height: 80vw;
    margin: 0 auto;
    img {
      .pos-ab();
      border-radius: 50%;
      &.thumb {
        transform: scale(0.8);
      }
    }
    animation: rotate 8s linear infinite;
    animation-play-state: paused;
    &.playing {
      animation-play-state: running;
      //小心层级问题
    }
  }
}
</style>