<template>
  <!-- <div>CardList</div> -->
  <li @click="$router.push('/playlist?id=' + item.id)" class="card-item">
    <div class="thumb">
      <img
        class="bg"
        :src="`${item.picUrl}?imageView=1&type=webp&thumbnail=246x0`"
      />
      <span
        ><img
          style="width: 10px"
          src="data:image/svg+xml;base64,"
          alt=""
        />
        {{ item.playCount | parseCount }}</span
      >
    </div>
    <p>{{ item.name }}</p>
  </li>
</template>

<script>
export default {
  props: {
    //接收父级传下来的item内容
    item: {
      type: Object, //验证
      required: true,
    },
  },
    filters: {
    parseCount: function(value) {
      // return value + "0";
      if (value > 100000000) {//判断亿
        return (value / 100000000).toFixed(1) + "亿";//计算亿
      } else if (value > 10000) {
        return (value / 10000).toFixed(1) + "万";
      } else {
        return value;
      }
    },
  },
};
</script>

<style lang="less" scoped>
.card-item {
  flex-basis: 25%;//均分
  flex-grow: 1;//占1

  &:nth-child(3n + 2) {
    // background: red;
    margin: 0 5px;//中间加间距
  }

  .thumb {
    position: relative;//相对定位 
    top: 0;
    left: 0;
    background: red;
    img.bg {
      vertical-align: middle;
      width: 100%;//图片占100
    }
    span {
      position: absolute;//定位绝对 右上角
      top: 5px;
      right: 5px;
      font-size: 10px;
      color: white;
    }
  }
  p {
    font-size: 13px;
    // padding: 50px 10px;
    margin: 5px;
    display: -webkit-box;//省略号
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;//占两行
    overflow: hidden;//超出隐藏
    text-overflow: ellipsis; //text-overflow当文本溢出包含元素时发生的事情。//省略号ellipsis

    // line-height: 2;
  }
}
</style>