import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'//推荐页面
import Search from '../views/Search.vue'//搜索页面
import PlayList from '../views/PlayList.vue'//

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: { showNavBar: true },//1.头部显示
  },
  {
    path: '/hot',
    name: 'Hot',
    // route level code-splitting
    // this generates a separate chunk (Hot.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "Hot" */ '../views/Hot.vue'),
     meta: { showNavBar: true },//1.头部显示
  },
  {
    path: '/search',
    name: 'Search',
    component: Search,
    meta: { showNavBar: true },//1.头部显示
  },
  {
    path: '/playlist',
    name: 'PlayList',
    component: PlayList
  },
]

const router = new VueRouter({
  routes
})

export default router
