<template>
  <div class="lyric" @click="$emit('toggle-show-play-back', false)" ref="lyric">
    <!-- {{ currentSongId }} -->
    <!-- lyric -->
    <ul
      class="content"
      ref="lyricContent"
      v-if="parsedLyric.length"
      :style="{ marginTop: -scrollH + 'px' }"
    >
      <li v-for="(l, i) in parsedLyric" :key="i">
        <span
          :style="{
            animationDuration: parsedLyric[i + 1]
              ? parsedLyric[i + 1].time - l.time - 0.3 + 's'
              : '3s',
          }"
           :class="{
              active: currentLyricIndex === i,
              playing: playing && currentLyricIndex === i,
            }"
          >{{ l.text }}</span
        >
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    playing: Boolean,
    currentSongId: Number,
    currentSong: Object,
    currentTime: Number, //歌曲时间
  },
  data: function () {
    return {
      lyric: null, //歌词为null
      // showplayback: true,
      personalizeds: true,
      lisH: [], //存dom高度
      scrollH: 0,
    };
  },
  watch: {
    "currentSong.id": function (id) {
      // if(n&&lyric){
      //   this.axios.get("http://apis.netstart.cn/music/lyric",{
      //     id:this.currentSongId
      //   })
      // }
      // console.log(id);
      this.getLyric(id); //上下一首歌词
    },
    currentLyricIndex: function (index) {
      //  console.log(index,this.$refs.lyricContent)//整个dom
      // var lis = this.$refs.lyricContent
      //   ? this.$refs.lyricContent.querySelectorAll("li")
      //   : null;//querySelectorAll判断存不存在
      // console.log(index);//小心报错，querySelectorAll不存在所以报错（可以先判断存不存在）
      // 当前歌词前面所有的歌词的高度
      var h = this.lisH.slice(0, index).reduce(function (total, item) {
        return total + item;
      }, 0);
      // console.log(h); //打印高度
      h = h > 200 ? h - 200 : 0; //开始在中间
      h = h > 1928 ? 1928 : h; //结束在中间
      this.scrollH = h;
    },
    parsedLyric: function () {
      // console.log("parsedLyric改变", this.$refs.lyricContent);//拿不到dom this.$refs.lyricContent=undefined
      this.$nextTick(() => {
        // console.log("nextTick", this.$refs.lyricContent);//能拿到dom
        var lis = this.$refs.lyricContent.querySelectorAll("li"); // lis类数组
        this.lisH = [...lis].map((item) => item.offsetHeight); //[...lis]转为数组   得到高度
      });
    },
  },
  created: function () {
    this.getLyric(this.currentSong.id);
    //  console.log(this.$refs.lyricContent)//undefined拿不到。上面可以（要等到）
  },
  methods: {
    getLyric: function (id) {
      this.axios
        .get("https://apis.netstart.cn/music/lyric", {
          params: {
            id,
          },
        })
        .then((res) => (this.lyric = res.data.lrc.lyric));
    },
  },
  computed: {
    //计算属性处理歌词
    parsedLyric: function () {
      //上面遍历parsedLyric
      if (this.lyric) {
        return this.lyric
          .split("\n")
          .filter((s) => s)
          .map((s) => {
            var text = s.replace(/^\[\d{2}:\d{2}\.\d{2,3}\]/i, ""); //拆出文字  “”替换为空
            var timeStr = s.replace(text, "").replace(/(^\[|\]$)/g, ""); //拆了文字再拆括号   /g替换多个
            var timeArr = timeStr.split(":").map((item) => Number(item)); //拆完括号拆数字
            var time = timeArr[0] * 60 + timeArr[1];
            return { text, time };
          }); //filter去掉最后一个
      } else {
        return [];
      }
    },
    currentLyricIndex: function () {
      //高亮
      var i = this.parsedLyric.findIndex(
        (item) => item.time > this.currentTime
      );
      var currentLyricIndex = i !== -1 ? i - 1 : this.parsedLyric.length - 1;
      return currentLyricIndex;
    },
  },

  // created: function () {
  //   this.axios
  //     .get(`http://apis.netstart.cn/music/lyric?id=${this.currentSongId}`)
  //     .then((res) => {
  //       // console.log(this,res)
  //       var personalizeds= res.data.lrc.lyric;
  //       console.log(personalizeds)

  //       // this.personalizeds = res.data.lrc.lyric;
  //       // console.log(this.personalizeds)
  //       // var arr = this.personalizeds
  //       //   .split("\n")
  //       //   .filter((s) => s)
  //       //   .map((s) => {
  //       //     var text = s.replace(/^\[\d{2}:\d{2}\.\d{3}\]/i, ""); //拆出文字  “”替换为空
  //       //     var timeStr = s.replace(text, "").replace(/(^\[|\]$)/g, ""); //拆了文字再拆括号   /g替换多个
  //       //     var timeArr = timeStr.split(":").map((item) => Number(item)); //拆完括号拆数字
  //       //     var time = timeArr[0] * 60 + timeArr[1];
  //       //     return { text, time };
  //       //   }); //filter去掉最后一个
  //       // console.log(lis)
  //       // console.log(arr)
  //       // 显示歌词
  //       // arr.forEach((item) => {
  //       //   var node = document.createElement("li");
  //       //   node.innerText = item.text;
  //       //   document.querySelector(".box ul").appendChild(node);
  //       // });
  //       // document.querySelector("audio").ontimeupdate = function () {
  //       //   // console.log(document.querySelector("audio").currentTime);//当前播放时间

  //       //   var t = document.querySelector("audio").currentTime; //得到当前播放时间
  //       //   //var index = arr.findIndex(item => item.time > t);//查找索引，获取大于当前播放时间的第一句
  //       //   //console.log(arr[index - 1].text)//大于当前时间的第一句的上一句（就是当前播放那句）

  //       //   var i = arr.findIndex((item) => item.time > t);
  //       //   //播放时找不到就会是负1
  //       //   var currentLyricIndex = i !== -1 ? i - 1 : arr.length - 1;
  //       //   //判断当前歌曲不等于-1 就取当前歌曲index减1 如果等于负1就取最后一个arr.length - 1
  //       //   console.log(arr[currentLyricIndex].text);

  //       //   document.querySelector(".box ul").style.marginTop =
  //       //     -currentLyricIndex * 25 + "px"; //高度变化

  //       //   document.querySelectorAll(".box ul li").forEach((item, index) => {
  //       //     if (index === currentLyricIndex) {
  //       //       item.style.color = "red";
  //       //     } else {
  //       //       item.style.color = "black";
  //       //     }
  //       //   });
  //       // };
  //     });
  // },
};
</script>

<style lang="less" scoped>
@keyframes xxx {
  from {
    background-position-x: 0;
  }
  to {
    background-position-x: -100%;
  }
}
.pos-ab() {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.lyric {
  width: 100vw;
  height: 100vw;
  text-align: center;
  // background-color: yellow;
  //  background: linear-gradient(to bottom,red 10%,transparent 10%,transparent 90%,blue 90%);
  overflow: hidden;
  position: relative;
  // &::before {
  //   content: "";
  //   .pos-ab();
  //   background: linear-gradient(
  //     to bottom,
  //     black 10%,
  //     transparent 10%,
  //     transparent 90%,
  //     black 90%
  //   );
  //   -webkit-background-clip: text;
  //   -webkit-text-fill-color: red;
  // }
  ul {
    transition: all 0.3s;
    li {
      line-height: 1.8;
      span {
        &.active {
          animation-name: xxx;
          animation-timing-function: linear;
          animation-duration: 3s;
          background-image: linear-gradient(
            to right,
            rgba(236, 184, 95, 0.959) 60%,
            rgba(59, 230, 150, 0.938) 60%
          );
          background-size: 200%;
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
          animation-play-state: paused;
          &.playing {
            animation-play-state: running;
          }
        }
      }
    }
  }
}
</style>