<template>

  <div class="home">
    
    <!-- <img alt="Vue logo" src="../assets/logo.png" /> -->
    <!-- <HelloWorld msg="Welcome to Your Vue.js App"/> -->
    <HomeTitle>编辑推荐</HomeTitle>
    <!-- <ul class="home-cardlist">
      <CardList
        v-for="item in personalizeds"
        :key="item.id"
        @click.native="$router.push('/playlist?id=' + item.id)"
      />
      .native原生dom事件
    </ul> -->
    <ul class="home-cardlist">
      <CardList v-for="item in personalizeds" :key="item.id" :item="item" />
    </ul>
    <!-- 组件不添加点击，在cardlist加 -->
    <!-- <ul>
      <li
        v-for="item in personalizeds"
        :key="item.id"
        @click="$router.push('/playlist?id=' + item.id)"
      >
        {{ item.name }}
      </li>
    </ul> -->

    <HomeTitle>最新音乐</HomeTitle>
    <!-- 组件复用 -->

    <ul class="home-songlist">
      <SongList
        v-for="item in newsongs"
        :key="item.id"
        :item="item"
        :currentSongId="currentSongId"
        @change-current-song="
        $emit('change-current-song', $event); 
        $emit('change-current-play-list', newsongs);"
        :playing="playing"
      ></SongList>
    </ul>
  </div>
  <!-- 第二：使用组件模板位置 -->
</template>


<script>
//第一：引用
import HomeTitle from "@/components/HomeTitle.vue";
import CardList from "@/components/CardList.vue";
import SongList from "@/components/SongList.vue";

export default {
  name: "Home",
  components: {
    //第三：注册
    HomeTitle,
    CardList,
    SongList,
  },
  props: {//接收父传来的id
    currentSongId: {
      type: Number,
    },
    playing:Boolean,//接收音频事件，并且还往下传   来实现动画效果
  },
  data: function () {
    return {
      personalizeds: [],
      newsongs: [], //最新音乐初始
    };
  },
  created: function () {
    //组件最早创建
    // 发起Ajax请求  因为这里最早可以访问this  就是页面一加载完就可以调用
    this.axios
      .get("https://apis.netstart.cn/music/personalized?limit=6")
      .then((res) => {
        // console.log(this,res)//拿到数据
        this.personalizeds = res.data.result; //得到数据上面就遍历显示
      });

    // 最新音乐  两个axios可能会有先后顺序 前面一个卡死，后面就不执行了  所以实际需要考虑先后问题
    this.axios
      .get("https://apis.netstart.cn/music/personalized/newsong")
      .then((res) => {
        // console.log(this,res)//查看数据
        this.newsongs = res.data.result; //得到数据  上面就遍历显示
      });
  },
};
</script>
<style lang="less">
.home-cardlist {
  display: flex;
  flex-wrap: wrap; //换行
}
</style>