<template>
  <div v-if="detail" class="pi">
    <div @click="backHistory" class="return">
      <svg
        t="1616725231688"
        class="icon"
        viewBox="0 0 1026 1024"
        version="1.1"
        xmlns="http://www.w3.org/2000/svg"
        p-id="6368"
        width="30"
        height="30"
      >
        <path
          d="M1.024 512c0 282.624 229.376 512 512 512s512-229.376 512-512-229.376-512-512-512-512 229.376-512 512z"
          fill="#D3B473"
          opacity=".59"
          p-id="6369"
        ></path>
        <path
          d="M762.88 736.768s-5.12 13.312-11.776 0c0 0-75.776-228.352-260.608-171.008v71.168s-3.072 41.472-39.424 14.336L269.824 494.592s-38.4-20.992 2.56-49.664L455.68 287.232s27.648-19.456 34.304 12.8v76.8c0-0.512 344.064 16.384 272.896 359.936z"
          fill="#FFFFFF"
          p-id="6370"
        ></path>
      </svg>
    </div>
    <section class="plhead">
      <div
        class="bg"
        :style="{
          backgroundImage: `url(${detail.coverImgUrl}?param=170y170)`,
        }"
      ></div>
      <div class="wrap">
        <div class="fl">
          <img
            class="u-img"
            :src="`${detail.coverImgUrl}?imageView=1&type=webp&thumbnail=378x0`"
          />
          <span class="icon">歌单</span
          ><span class="erji"
            ><img
              style="width: 12px"
              src="data:image/svg+xml;base64,"
            />
            {{ detail.playCount | parseCount }}</span
          >
        </div>
        <div class="fr">
          <h2 class="title">
            {{ detail.name }}
          </h2>
          <div class="auth">
            <img
              class="u-img"
              :src="`${detail.creator.avatarUrl}?imageView=1&amp;type=webp&amp;thumbnail=90x0`"
            />
          </div>
        </div>
      </div>
    </section>
    <!-- <h2>PlayList{{ $route.query.id }}</h2> -->
    <ul>
      <!-- <li v-for="item in detail.tracks" :key="item.id">{{item.name}}</li> -->
      <!-- 复用songlist组件 -->
      <SongList
        v-for="(item, index) in detail.tracks"
        :key="item.id"
        :item="item"
        @change-current-song="
          $emit('change-current-song', item);
          $emit('change-current-play-list', detail.tracks);
        "
        :currentSongId="currentSongId"
        :playing="playing"
        :class="{ lt3: index < 3 }"
      >
        {{ index + 1 }}</SongList
      >
    </ul>
  </div>
</template>

<script>
import SongList from "@/components/SongList.vue";

export default {
  components: {
    //第三：注册
    SongList,
  },
  props: {
    //playlist也接收home状态  并且也往下传
    currentSongId: {
      type: Number,
    },
    playing: Boolean, //接收父传下来的音频事件 实现动画
  },
  data: function () {
    return {
      detail: null, //初始detail  因为为空，dom还没更新  遍历时会报错  需要判断if
    };
  },
  methods: {
    getPlayListDetail: function (id) {
      //根据上面id拿数据  写方法
      this.axios
        .get("https://apis.netstart.cn/music/playlist/detail", {
          params: {
            id, //传来的参数id
          },
        })
        .then((res) => {
          // console.log(res);
          this.detail = res.data.playlist; //得到detail的数据
        });
    },
    backHistory() {
      this.$router.go(-1);//路由负1
    },
  },
  created: function () {
    this.getPlayListDetail(this.$route.query.id);
  },
  filters: {
    parseCount: function (value) {
      // return value + "0";
      if (value > 100000000) {
        //判断亿
        return (value / 100000000).toFixed(1) + "亿"; //计算亿
      } else if (value > 10000) {
        return (value / 10000).toFixed(1) + "万";
      } else {
        return value;
      }
    },
  },
};
</script>

<style lang="less" scoped>
.pi {
  position: relative;
  .return {
    position: absolute;//记得改fixed
    top: 0;
    left: 15px;
    z-index: 0;
  }
  .plhead {
    width: 100vw;
    height: 186px;
    position: relative;
    padding: 30px 10px 30px 15px;
    overflow: hidden;
    z-index: -9;
    .bg {
      width: 100%;
      height: 100%;
      background-repeat: no-repeat;
      background-size: cover;
      background-position: 50%;
      filter: blur(20px);
      transform: scale(1.5);
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;

      &::after {
        content: "";
        display: block;
        background-color: rgba(0, 0, 0, 0.25);
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
      }
    }
    .wrap {
      display: flex;
      position: relative;
      z-index: 2;
      .fl {
        width: 126px;
        height: 126px;
        position: relative;
        .u-img {
          width: 100%;
          vertical-align: middle;
        }
        .icon {
          position: absolute;
          z-index: 3;
          top: 10px;
          left: 0;
          padding: 0 8px;
          height: 17px;
          color: #fff;
          font-size: 9px;
          text-align: center;
          line-height: 17px;
          background-color: rgba(217, 48, 48, 0.8);
          border-top-right-radius: 17px;
          border-bottom-right-radius: 17px;
        }
        .erji {
          position: absolute;
          right: 2px;
          top: 0;
          z-index: 3;
          padding-left: 15px;
          color: #fff;
          font-size: 12px;
          background-position: 0;
          background-repeat: no-repeat;
          background-size: 11px 10px;
          text-shadow: 1px 0 0 rgb(0 0 0 / 15%);
        }
      }
      .fr {
        flex: 1 1 auto;
        width: 1%;
        margin-left: 16px;
        .title {
          padding-top: 1px;
          font-size: 17px;
          line-height: 1.3;
          color: #fefefe;
          height: 44px;
          display: -webkit-box;
          -webkit-box-pack: center;
        }
        .auth {
          display: inline-block;
          color: hsla(0, 0%, 100%, 0.7);

          .u-img {
            width: 30px;
            height: 30px;
            vertical-align: middle;
            border-radius: 50%;
            margin-right: 5px;
          }
        }
      }
    }
  }
  h3 {
    height: 23px;
    line-height: 23px;
    padding: 0 10px;
    font-size: 12px;
    color: #666;
    background-color: #eeeff0;
  }
}
</style>