<template>
  <div class="search" @scroll="scroll">
    <input
      class="inp"
      type="text"
      placeholder="搜索"
      v-model.trim="value"
      @keyup.enter="value && (searching = true)"
      @focus="inputing = true"
      @blur="inputing = false"
    />

    <section class="hots" v-if="!value && !searching">
      <h5>热门搜索</h5>
      <ul>
        <li
          v-for="hot in hots"
          :key="hot.first"
          @click="
            searching = true;
            value = hot.first;
          "
        >
          {{ hot.first }}
        </li>
      </ul>
      <h5>搜索记录</h5>
      <ol>
        <!-- <i class="bi bi-clock-history"></i> -->
        <li
          v-for="h in history"
          :key="h"
          @click="
            searching = true;
            value = h;
          "
        >
          <i class="bi bi-clock-history"></i>
          <span class="bis">{{ h }}</span>
          <span class="bi bi-x" @click.stop="deletes(h)"></span>
        </li>
      </ol>
    </section>

    <section class="suggests" v-if="value && !searching">
      <h5>搜索列表</h5>
      <ul>
        <li
          v-for="(item, index) in suggests"
          :key="index"
          @click="
            searching = true;
            value = item.keyword;
          "
        >
          {{ item.keyword }}
        </li>
      </ul>
    </section>

    <section class="suggests" v-if="searching">
      <h5>搜索结果</h5>
      <ul>
        <!-- <li v-for="(item, index) in searchResults" :key="index">
          {{ item.name }}
        </li> -->
        <SongList
          v-for="(item,index) in searchResults"
          :key="item.id"
          :item="item"
          :currentSongId="currentSongId"
          @change-current-song="
            $emit('change-current-song', $event);
            $emit('change-current-play-list', searchResults);
          "
          :playing="playing"
          >{{ index + 1 }}</SongList
        >
      </ul>
      <p v-if="!hasMore">没有更多了</p>
    </section>
  </div>
</template>

<script>
import SongList from "@/components/SongList.vue";
export default {
  components: {
    //第三：注册
    // HotSong,
    SongList,
  },
  props: {
    //接收父传来的id
    currentSongId: {
      type: Number,
    },
    playing: Boolean, //接收音频事件，并且还往下传   来实现动画效果
  },
  data: function () {
    return {
      hots: [],
      suggests: [], //根据搜索的值一开始为空
      searchResults: [], //按回车时变化
      value: "",
      searching: false, //按回车
      inputing: false, //监听是否在输入内容 然后判断searching是false还是true
      page: 0, //默认第0页
      hasMore: false, //用来触底
      history: JSON.parse(window.localStorage.getItem("history")) || [], //历史记录  而且放在本地存储  判断第一次为空
    };
  },

  // 滚动
  methods: {
    //可以做节流
    scroll: function (event) {
      //   console.log(event);
      if (this.hasMore) {
        if (
          event.target.offsetHeight + event.target.scrollTop ===
          event.target.scrollHeight
        ) {
          console.log("触底");

          this.getSearch(); //调用方法
        }
      } else {
        console.log("没有更多了");
      }

      //   document.querySelector(".search").scrollHeight;//全部高度
      //   document.querySelector(".search").scrollTop;//滚动高度
      //   document.querySelector(".search").offsetHeight;//目标高度
    },
    getSearch: function () {
      //一个方法
      this.axios
        .get("https://apis.netstart.cn/music/search", {
          params: {
            keywords: this.value,
            limit: 30,
            offset: this.page * 30,
          },
        })
        .then((res) => {
          this.searchResults.push(...res.data.result.songs); //因为push返回是一个数组
          this.page++;
          this.hasMore = res.data.result.hasMore;
        });

      // 历史记录
      // this.history.push(this.value)
      this.history = [...new Set([...this.history, this.value])]; //set去重记录  []包裹...this.history
      window.localStorage.setItem("history", JSON.stringify(this.history)); //放在本地存储
    },
    deletes: function (value) {
      this.history = this.history.filter((item) => item !== value);
      window.localStorage.setItem("history", JSON.stringify(this.history));
    },
  },
  // 滚动

  created: function () {
    this.axios.get("https://apis.netstart.cn/music/search/hot").then((res) => {
      //then写法
      this.hots = res.data.result.hots; //获取到数据
    });
  },

  // watch监听value变化
  watch: {
    // 搜索框
    value: function (n) {
      //监听value的变化时，如果不搜索时
      if (this.inputing) {
        //监听是否在输入内容 然后判断searching是false还是true
        this.searching = false;
      }
      if (n && !this.searching) {
        this.axios
          .get("https://apis.netstart.cn/music/search/suggest", {
            params: {
              keywords: n,
              type: "mobile",
            },
          })
          .then((res) => {
            //根据搜索到的值实现效果
            this.suggests = res.data.result.allMatch;
          });
      } else {
        //判断搜索框没有值时
        this.suggests = []; //suggests就是空
      }
    },
    //按回车时searching变化
    searching: function (n) {
      if (n && this.value) {
        //如果value为空
        this.getSearch(); //调用方法
      } else {
        this.searchResults = [];
      }
    },
  },
  // watch监听value变化
};
</script>

<style lang="less" scoped>
.search {
  padding: 15px;
  .inp {
    width: 80%;
    height: 25px;
    margin: 8px 2px;
    border-radius: 10px;
  }
  .hots {
    h5 {
      margin: 5px;
    }
    ul {
      overflow: hidden;
      li {
        float: left;
        margin: 5px;
        padding: 8px 18px;
        line-height: 8px;
        font-size: 12px;
        border: 1px solid rgba(177, 174, 174, 0.822);
        border-radius: 28px;
      }
    }
    ol {
      // padding: 10px 0px;
      list-style: none;
      li {
        display: flex;
        align-items: center;
        align-content: center;
        justify-content: center;
        margin-top: 10px;
        border-bottom: 1px solid rgba(221, 216, 216, 0.801);
        i {
          // flex: 1;
          // display: flex;
          margin: 0 5px;
        }
        .bis {
          flex: 1;
          // margin: 0 10px;
          // display: flex;
          // margin-left: 10px;
        }
      }
    }
  }
}
</style>